import React, { useState } from 'react';
import { Avatar, Button, Paper, Grid, Typography, Container } from '@material-ui/core'; 
import LockOutLinedIcon from '@material-ui/icons/LockOutlined';
import { GoogleLogin } from 'react-google-login';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import Icon from './Icon';
import useStyles from './styles';
import Input from './Input';
import { signin, signup } from '../../actions/auth';

const initialState = {
  firstName: '',
  lastName: '',
  email: '',
  password: '',
  confirmPassword: ''
};

const Auth = () => {

    const state = null;
    const classes = useStyles();
    const [showPassword, setShowPassword] = useState(false);
    const [isSignup, setIsSignup] = useState(false);
    const [formData, setFormData] = useState(initialState);
    const dispatch = useDispatch();
    const navigate = useNavigate();

    const handleSubmit = (e) => {
      e.preventDefault();
      // console.log(formData);
      if(isSignup) {
        //sign up the user
        dispatch(signup(formData, navigate));
      }else {
        dispatch(signin(formData, navigate));
      }
    };

    const handleChange = (e) => {
      setFormData({ ...formData, [e.target.name]: e.target.value});
    };
    
    const handleShowPassword = () => setShowPassword((prevShowPassword) => !prevShowPassword);
  
    const switchMode = () => {
      setIsSignup((prevIsSignup) => !prevIsSignup);
      setShowPassword(false);
    };
  
    const googleSuccess = async(res) => {
      const result = res?.profileObj; // if ? is not used, an error will be invoked saying the property of the obbject doesn't exist if no profile is returned in the response

      const token = res?.tokenId;
      try {
        dispatch( {type: 'AUTH', data: { result, token }} );
        
        navigate('/');


      } catch (error) {
        console.log(error);
      }

      // console.log(res);
    };

    const googleFailure = (error) => {
      console.log(error);
      console.log("Google Sign In was unsuccessful. Try again later");
    };

  return (
    <Container
      component="main"
      maxWidth="xs"
      >
        <Paper className = {classes.paper} elevation={3}>
          <Avatar className={classes.avatar}>
            <LockOutLinedIcon />
          </Avatar>
          <Typography variant='h5'>{isSignup? 'Sign up' : 'Sign in'}</Typography>
          <form className={classes.form} onSubmit={handleSubmit}>
            <Grid container spacing={2}>
              {
                isSignup && (
                  <>
                    <Input 
                      name='firstName' 
                      label='First Name' 
                      handleChange={handleChange}
                      autoFocus
                      half
                    />
                    <Input 
                      name='lastName' 
                      label='Last Name' 
                      handleChange={handleChange}
                      half
                    />
                  </>
                )
              }
              <Input 
                name="email" 
                label="Email Address" 
                handleChange={handleChange} 
                type='email' 
              />
              <Input 
                name="password" 
                label="Password" 
                handleChange={handleChange} 
                type={showPassword ? "text":"password"} 
                handleShowPassword={handleShowPassword}
              />
              { isSignup && 
                <Input 
                  name='confirmPassword' 
                  label='Repeat password' 
                  handleChange={handleChange}
                  type={showPassword ? "text":"password"} 
                  handleShowPassword={handleShowPassword}
                />
                }
            </Grid>
            <Button 
              type="submit"
              variant='contained'
              fullWidth
              color='primary'
              className={classes.submit}
              onClick={handleSubmit}
            >
              { isSignup ? 'Sign Up' : 'Sign In'}
            </Button>
            <GoogleLogin 
              clientId = "718702677209-s5vgok6tmrsd5gcjskffo7i1esdbbhde.apps.googleusercontent.com"
              render={(renderProps) => (
                <Button 
                  className={classes.googleButton} 
                  color='primary' 
                  fullWidth 
                  onClick={renderProps.onClick} 
                  disabled={renderProps.disabled} 
                  startIcon={<Icon />}
                  variant='contained'
                >
                  Google Sign In
                </Button>
              )}
              onSuccess={googleSuccess}
              onFailure={googleFailure}
              cookiePolicy='single_host_origin'
            />
            <Grid container justify = 'flex-end'>
              <Grid item>
                <Button onClick = {switchMode}>
                  { isSignup ? 
                    'Already have an account? Sign In' 
                    : 
                    "Don't have an account? Sign Up"
                  }
                </Button>
              </Grid>
            </Grid>
          </form>
        </Paper>
      </Container>
  );
};

export default Auth;
